<template>
  <v-card>
    <v-card-title>
      Создание партнера
    </v-card-title>
    <v-card-text>
      <v-text-field
          :disabled="wait"
          label="Имя"
          v-model="value.Name"
          @keyup="change"
          single-line
      />
    </v-card-text>
    <v-card-actions>
      <div class="flex-grow-1"></div>
      <v-btn
          :disabled="wait"
          color="primary"
          text
          @click="save_local"
      >
        Сохранить
      </v-btn>
      <v-btn
          :disabled="wait"
          color="primary"
          text
          @click="cancel"
      >
        Закрыть
      </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
  // import Vue from "vue"
  import {localStatuses} from '../../store'

  export default{
    name: "EditDialog",
    props: {
      "value": Object,
      "status": Number,
      "save": Function,
      "params": {
        type: Array,
        default: ()=>[]
      }
    },
    data: () => ({
      // edit: false,
      // editing_value: "",
    }),
    computed: {
      wait() {
        return this.status === localStatuses.WAIT
      }
    },
    methods: {
      select() {
        if(~[localStatuses.CHANGED, localStatuses.EDITOR, localStatuses.WAIT].indexOf(this.status)) return

        this.$emit("update:status", localStatuses.EDITOR)
        // this.editing_value = this.value
        // this.edit = true
      },
      close() {
        // this.edit = false
        this.$emit("update:status", localStatuses.VIEW)
        this.$emit("endEdited")
      },
      async cancel() {
        // проверка отмены отредактированных данных...
        if(this.status === localStatuses.CHANGED) {
          await this.$modal.confirm('У вас есть несохраненные изменения, точно закрыть?') && this.close()
        }
        else
          this.close()
      },
      change(){
        this.$emit("update:status", localStatuses.CHANGED)
      },
      save_local() {
        if(this.status !== localStatuses.CHANGED) return // Отмена сохранения неизмененных данных
        this.$emit("update:status", localStatuses.WAIT)
        this.save(this.value, ...this.params).then(()=>{
          this.close()
        })
      }
    }
  }
</script>
