<template>
  <v-dialog :value="visible" @input="close">
    <v-card v-if="wait">
      <v-card-text>
        <Spinner />
      </v-card-text>
    </v-card>
    <v-card v-else>
      <v-card-title>
        Партнер: {{ item.Name }}
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="6" class="py-0">
              <v-row>
                <v-col cols="6">
                  Количество цессий:
                </v-col>
                <v-col cols="6">
                  {{ item.cessionsCount }}
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6" class="py-0">
                  Количество запросов:
                </v-col>
                <v-col cols="6" class="py-0">
                  {{ item.queryCount }}
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="6" class="py-0">
              <v-row>
                <v-col cols="6">
                  Количество договоров:
                </v-col>
                <v-col cols="6">
                  {{ item.contractsCount }}
                </v-col>
              </v-row>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
            <v-data-table
                :headers="headers"
                :items="item.cessionsStatisticsPartner"
            >
              <template #item.action="{ item }">
                <v-btn icon @click="viewCession(item)">
                  <v-icon>mdi-eye</v-icon>
                </v-btn>
              </template>
            </v-data-table>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <div class="flex-grow-1"></div>
        <v-btn
            color="primary"
            text
            @click="close"
        >
          Закрыть
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import {statuses} from '../../store'
import Spinner from '../../components/common/Spinner'
import {mapActions, mapGetters, mapState} from 'vuex'

export default {
  // props: ["item", "status"],
  components: {
    Spinner
  },
  data: () => ({
    headers: [
      {
        text: "Номер цессии",
        value: "Name"
      },{
        text: "Кол-во сканов цессии",
        value: "CessionScansCount"
      },{
        text: "Кол-во кредитных договоров",
        value: "contractsCount"
      },{
        text: "Договоров без сканов",
        value: "contractsCountWithoutScans"
      },{
        sortable: false,
        value: "action",
        width: 1
      }
    ],
    visible: false
  }),
  computed: {
    ...mapGetters("partners", ['getById']),
    ...mapState("partners", ["status", "error"]),
    id() {
      return parseInt(this.$route.params.id)
    },
    item() {
      return this.getById(this.id)
    },
    wait() {
      return this.status === statuses.WAIT
    }
  },
  async mounted() {
    this.visible = true
    try {
      await this.GET_ITEM(this.id)
    } catch (e) {
      this.$bus.$emit("sendError", e.message)
    }
  },
  methods: {
    ...mapActions("partners", ["GET_ITEM"]),
    viewCession(cession) {
      this.$router.push({ name: 'cession', params: { id: cession.Id } })
    },
    close() {
      this.$router.push({ name: 'partners' })
    }
  }
}
</script>
