<template>
  <div @click="select">
    <div class="wrapper" v-if="!edit">
      <slot />
    </div>
    <div v-else>
      <p style="margin: 0;" v-if="wait">Сохраняем...</p>
      <v-text-field
          v-else
          v-model="editing_value"
          hide-details
          single-line
          dense
          @keyup.enter="save_local"
          @keyup.esc="cancel"
          @keyup="change"
          class="editor_table"
          :disabled="wait"
      >
        <template #append-outer>
          <v-btn icon @click.stop="cancel" :disabled="wait"><v-icon color="warning" style="cursor: pointer">mdi-cancel</v-icon></v-btn>
          <v-btn icon @click.stop="save_local" :disabled="wait"><v-icon color="primary" style="cursor: pointer">mdi-content-save</v-icon></v-btn>
        </template>
      </v-text-field>
    </div>
  </div>
</template>
<script>
// import Vue from "vue"
import {localStatuses} from '../../store'

//:TODO Реализовать проверку, есть ли в родительском компоненте миксин localStatus

export default{
  name: "EditDialog",
  props: {
    "value": String,
    "status": Number,
    "save": Function,
    "params": {
      type: Array,
      default: ()=>[]
    },
  },
  data: () => ({
    edit: false,
    editing_value: "",
  }),
  computed: {
    wait() {
      return this.status === localStatuses.WAIT
    }
  },
  methods: {
    select() {
      if(~[localStatuses.CHANGED, localStatuses.EDITOR, localStatuses.WAIT].indexOf(this.status)) return

      this.$emit("update:status", localStatuses.EDITOR)
      this.editing_value = this.value
      this.edit = true
    },
    close() {
      this.edit = false
      this.$emit("update:status", localStatuses.VIEW)
    },
    async cancel() {
      // проверка отмены отредактированных данных...
      if(this.status === localStatuses.CHANGED) {
        await this.$modal.confirm('У вас есть несохраненные изменения, точно закрыть?') && this.close()
      }
      else
        this.close()
    },
    change(){
      this.$emit("update:status", localStatuses.CHANGED)
    },
    save_local() {
      if(this.status !== localStatuses.CHANGED) return // Отмена сохранения неизмененных данных
      this.$emit("update:status", localStatuses.WAIT)
      this.save(this.editing_value, ...this.params).then(()=>{
        this.close()
      })
    }
  }
}
</script>
<style scoped>
.wrapper{
  padding: 12px 0;
}
.editor_table{

}
/*button{*/
  /*height: 24px !important;*/
/*}*/
.margin0 {
  margin: 0;
}
</style>
