import {localStatuses, statuses} from '../store'

export default{
  data: () => (
    {
      localStatus: localStatuses.VIEW,
    }),
  // watch: {
  //   status(newVal) {
  //     newVal === statuses.READY
  //     this.localStatus = localStatuses.VIEW
  //   }
  // },
  computed: {
    view() {
      return this.localStatus === localStatuses.VIEW
    },
    editor() {
      return this.localStatus === localStatuses.EDITOR
    },
    changed() {
      return this.localStatus === localStatuses.CHANGED
    },
    wait() {
      return this.localStatus === localStatuses.WAIT
    }
  },
  methods: {
    setView() {
      return this.localStatus = localStatuses.VIEW
    },
    setEditor() {
      return this.localStatus = localStatuses.EDITOR
    },
    setChanged() {
      return this.localStatus = localStatuses.CHANGED
    },
    setWait() {
      return this.localStatus = localStatuses.WAIT
    }
  },
  async beforeRouteLeave (to, from, next) {
    if (!this.changed && !this.wait) {
      next()
      this.setView()
    }
    else {
      const answer = await this.$modal.confirm('Вы хотите уйти? У вас есть несохранённые изменения!')
      answer && (this.setView(), next())
    }
  }
}
