<template>
  <v-card class="funnyback">
    <v-card-text>
      <Table
          :headers="headers"
          :items="items"
          :status="status"
          :error="error"
        >
        <template #top>
          <v-toolbar flat color="white">
            <p class="subtitle-2 font-italic font-weight-medium">
              Название редактируется в таблице. Сохраняется по кнопке enter!
            </p>
            <div class="flex-grow-1"></div>
            <v-dialog
                v-model="addDialog.visible"
                :persistent="changed"
            >
              <template v-slot:activator="{ on }">
                <v-btn text v-on="on" @click="setEditor" :disabled="editor || changed || wait">
                  Добавить
                </v-btn>
              </template>
              <EditPartner
                  v-model="addDialog.item"
                  :save="save"
                  :status.sync="localStatus"
                  @endEdited="addDialog.visible = false; addDialog.item = {}"
              />
            </v-dialog>
          </v-toolbar>
        </template>
        <template #item.Name="{ item }">
          <EditField
              v-model="item.Name"
              :save="save"
              :params="[item]"
              :status.sync="localStatus"
          > {{ item.Name }}
          </EditField>
        </template>
        <template #item.action="{ item }">
          <v-btn icon @click="viewItem(item)">
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </template>
      </Table>
      <router-view v-if="ready"/>
    </v-card-text>
  </v-card>
</template>
<script>
import { mapState, mapActions } from "vuex"
import Table from "../../components/common/Table"
import EditField from "../../components/common/EditField"
import EditPartner from "../../components/Partners/EditPartner"
// import ViewPartner from './ViewPartner'
import localStatus from "../../mixins/localStatus"
import {statuses} from '../../store'

export default {
  mixins: [localStatus],
  components: {
    Table,
    EditField,
    EditPartner,
    // ViewPartner
  },
  data: () => ({
    // filters: {
    //   name: ""
    // },
    addDialog: {
      visible: false,
      item: {
        Name: ""
      }
    },
    // viewDialog: {
    //   visible: false,
    //   item: {}
    // },
    headers: [
      {
        text: "Id",
        value: "Id"
      },{
        text: "Наименование",
        value: "Name"
      },{
        sortable: false,
        value: "action",
        width: 1
      }
    ]
  }),
  computed: {
    ...mapState("partners", ["status", "error", "items"]),
    ready() {
      return this.status === statuses.READY
    }
    // filteredItems() {
    //   this.filters
    // }
  },
  mounted() {
    Promise.all([
      this.GET_ITEMS()
    ])
    this.status === statuses.READY && this.setView() // Повторный переход на страницу
  },
  methods: {
    ...mapActions("partners", ["GET_ITEMS", "SAVE_ITEM", "CREATE_ITEM"]),
    async save(value, item){
      try {
        const new_item = Object.assign({}, item)
        new_item.Name = value
        await this.SAVE_ITEM(new_item)
        this.$bus.$emit("sendSuccess", "Сохранено")
      } catch (e) {
        console.log(e)
        this.$bus.$emit("sendError", e.message)
      }
    },
    async viewItem(item){
      this.$router.push({ name: 'partner', params: { id: item.Id } })

      // try {
      //   this.viewDialog.visible = true
      //   await this.GET_ITEM(item.Id)
      //   this.viewDialog.item = item
      // } catch (e) {
      //   console.log(e)
      //   this.$bus.$emit("sendError", e.message)
      //   this.viewDialog.visible = false
      // }
    },
    async createItem(){
      try {
        await this.CREATE_ITEM(this.addDialog.item)
        this.$bus.$emit("sendSuccess", "Сохранено")
        this.addDialog.visible = false
        this.addDialog.item = {}
      } catch (e) {
        console.log(e)
        this.$bus.$emit("sendError", e.message)
      }
    }
  }
}
</script>
<style scoped>
  p{
    margin-bottom: 0 !important;
  }
</style>
